.blockStyle, .map, .streams__stream, .streams__main .-chat-wrapper {
    border: 1px solid #dcdcdc;
    border-radius: 3px;
    background-color: #fff;
}

.-full, .map, .streams, .streams__item, .streams__stream, .streams__empty, .streams__large {
    width: 100%;
    height: 100%;
    position: relative;
}

.-v-scroll, .-send-messages-block .-sms-push .-added-names, .-send-messages-block .-history__content, .chat__wrapper {
