}

.shadowStream, .blockStyle, .map, .streams__stream, .streams__main .-chat-wrapper, .streams__main .-name-block {
    box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.22);
}

.shadowRight, .message-feed {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.22);
}

.headerSeparator, .header .police-status, .header .status-bar, .header__button {
    border-left: 1px solid #d95b1f;
}

.headerSeparatorCalled, .header.-active .police-status,
.header.-active .status-bar,
.header.-active .header__button {
    border-left: 1px solid #aa2b2b;
}

.headerLogo, .header__logo {
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.22);
}

.headerStatus, .header__status-text {
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3);
}

.innerShadow, .header__container {
    box-shadow: inset 0 -2px 2px 0 rgba(0, 0, 0, 0.22);
}

.innerStreamTop {
    box-shadow: inset 0 -2px 2px 0 rgba(0, 0, 0, 0.22);
}

.innerMenuShadow, .menu__unit {
    box-shadow: inset 0 -2px 2px 0 rgba(0, 0, 0, 0.12);
}

