.button {
    display: inline-block;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
}

.button.-answer,
.button.-on-hold {
    border-radius: 3px;
    padding: 6px 15px;
    display: none;
    margin: auto;
}

.button.-answer.-active,
.button.-on-hold.-active {
    display: table;
}

.button.-answer.-inactive,
.button.-on-hold.-inactive {
    display: table;
}

.button.-answer,
.button.-answer.-active {
    border: 1px solid #2e9259;
    background: -webkit-linear-gradient(#3bb46e, #2ca661) top repeat;
    background: linear-gradient(#3bb46e, #2ca661) top repeat;
}

.button.-answer:hover,
.button.-answer.-active:hover {
    background: -webkit-linear-gradient(#48c37d, #3ab56f) left repeat;
    background: linear-gradient(#48c37d, #3ab56f) left repeat;
}

.button.-answer .button__text,
.button.-answer.-active .button__text {
    color: #fff;
}

.button.-inactive {
    border: 1px solid #ccc7c7;
    background: -webkit-linear-gradient(#fcfcfc, #eeeff0) top repeat;
    background: linear-gradient(#fcfcfc, #eeeff0) top repeat;
}

.button.-inactive:hover {
    background: -webkit-linear-gradient(#fff, #fcfcfc) left repeat;
    background: linear-gradient(#fff, #fcfcfc) left repeat;
}

.button.-inactive .button__text {
    color: #c2c2c2;
}

.button.-on-hold {
    border: 1px solid #b73334;
    background: -webkit-linear-gradient(#cb4545, #bd3638) top repeat;
    background: linear-gradient(#cb4545, #bd3638) top repeat;
}

.button.-on-hold:hover {
    background: -webkit-linear-gradient(#da5656, #cb4849) left repeat;
    background: linear-gradient(#da5656, #cb4849) left repeat;
}

.button.-on-hold .button__text {
    color: #fff;
}

.button.-mute.-volume {
    background-position: 0 0;
}

.button.-mute .-text {
    font-weight: bold;
    color: #666;
}

.button.-unmute.-volume {
    border-radius: 3px;
    padding: 6px 10px;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.22);
    background-color: #fff;
    border: 1px solid #ccc7c7;
}

.button.-unmute.-volume .-icon {
    background-position: 0 -44px;
}

.button.-unmute .-text {
    font-weight: bold;
    color: #666;
}

.button.-gray {
    border: 1px solid #b7b8bc;
    border-radius: 3px;
    padding: 7px 13px 5px;
