    background: linear-gradient(#f4f4f4, #d9dadb) top repeat;
}

.button.-gray .-text {
    font-weight: bold;
    line-height: 18px;
    color: #666;
}

.button.-gray:hover {
    background: -webkit-linear-gradient(#f4f4f4, #e1e2e3) top repeat;
    background: linear-gradient(#f4f4f4, #e1e2e3) top repeat;
}

.button.-gray.-active {
    background: none;
    background-color: #fff;
}

.button.-gray.-inactive {
    opacity: 0.5;
}

.button.-submit {
    background-color: #4e9ed4;
    color: #fff;
    border-radius: 3px;
    cursor: pointer;
    padding: 7px 15px;
    height: 32px;
}

.button.-submit:hover {
    background-color: #3e90c7;
}

.button.-submit.-inactive {
