}

.error__text:empty,
.error__message:empty {
    margin: 0;
    padding: 0;
    height: 0;
}

.error__block {
    border: 1px solid #db5858;
}

table .error__text {
    display: none;
}

.required:after {
    content: '*';
    color: #db5858;
    margin-left: 3px;
