    height: 18px;
    border-radius: 3px;
    display: block;
    position: absolute;
    top: 11px;
    right: 12px;
    border: solid 1px #a0a0a0;
}

.popover__close .crs {
    width: 8px;
    height: 2px;
    background: #a0a0a0;
    position: absolute;
    display: block;
    top: 7px;
    left: 4px;
    transform: rotate(45deg);
}

.popover__close .crs:after {
    content: '';
    position: absolute;
    display: block;
    width: 2px;
    height: 8px;
    background: #a0a0a0;
    top: -3px;
    left: 3px;
